.contact {
    font-family: "Quicksand", sans-serif;
    // /background-color:#eeecec ;
    width: 100%;
    text-align: center;

    color: #fffdfd;

    .container {

        .wrapper {

            .row {

                .title {

                    //border: 1px solid #000;
                    height: 80px;
                    width: 100%;


                }

                .heading {
                    width: 20%;


                    //margin-left: -100px;
                    h4 {
                        text-align: center;
                        font-size: 50px;
                        margin-top: 20px;
                        color: #606060;

                        span {
                            border-bottom: 4px solid #fffdfd;

                        }
                    }
                }
            }
        }
    }
}

.contact-content{
    background-image: url('../../../public/WBJ7EY.png');
    .container{
        .wrapper{
            .row{
                .contented{
                    display:flex ;
                    .left-side{
                        margin: 100px 0px 50px 0px;
                        width: 500px;
                       .title{
                        h5 {
                                font-size: 70px;
                                font-weight: bold;
                                color: #fff;
                        
                            }
                       }
                        .c-content{
                            ul{
                                li{
                                    list-style: none;
                                    // border: 1px solid #000;
                                    border: none;
                                    border-bottom:1px solid rgb(255, 253, 253) ;
                                    width: 350px;
                                    margin-left: -30px;
                                    padding: 10px 15px;
                                    background-color: #363636;
                                    color: #fff;
                                    i{
                                        color: #fff;
                                    }
                                }
                            }
                        }
                       
                    } 
                    .right {
                         margin-top: 120px;
                        background-color: #111111;
                        width: 50%;
                        padding: 10px 20px;
                        margin-bottom: 20px;
                        border-radius: 20px;
                        font-weight: bold;
                         .forms{
                            background-color: #111111;
                            color: #fff;
                            input{
                                width: 100%;
                                height: 50px;
                                border-radius: 15px;
                                border: 1px solid #eeecec;
                                background-color: #6d6d6d;
                                padding: 0px 15px;
                                margin-bottom: 20px;
                                color: #fff;
                               
                                
                            }

                            input::placeholder{
                                color: #ffffff;
                            }
                            textarea{
                                width: 100%;
                                    height: 50px;
                                    border-radius: 15px;
                                    border: 1px solid #eeecec;
                                    background-color: #6d6d6d;
                                    padding: 10px 15px;
                                    margin-bottom: 20px;
                                    height: 100px;
                                    resize: none;

                            }
                            textarea::placeholder{
                                color: aliceblue;
                               
                            }
                            
                         }
                     }
                }
            }
        }
    }
}
